<template>
  <div class="create edit-create">
    <!-- 头部 -->
    <div class="header">
      <div class="left"></div>
      <div class="right">
        <el-button @click="save">
          <span class="iconfont icon-baocun" style="font-size:15px;"></span>保存
        </el-button>
        <el-button type="primary">
          <span class="iconfont icon-fenxiang"></span>投放
        </el-button>
      </div>
    </div>
    <!-- content -->
    <div class="create-conent">
      <!-- 左侧 -->
      <div class="left">
        <!-- 单选 -->
        <div class="select-button">
          <el-button class="iconfont icon-danxuan" data-type="fill" @click="show_control">单选</el-button>
        </div>
        <!-- 多选 -->
        <div class="select-button">
          <el-button class="iconfont icon-duoxuan" data-type="multiple" @click="show_control">多选</el-button>
        </div>
        <!-- 填空 -->
        <div class="select-button">
          <el-button
            class="iconfont icon-plus-blanksfill"
            data-type="single"
            @click="show_control"
          >填空</el-button>
        </div>
      </div>
      <!-- 内容区_S-->
      <div class="middle">
        <!-- 标题 -->
        <h2 @click="titleClick" v-if="!titleChange">{{qsItem.title||"问卷标题(点击可修改)"}}</h2>
        <input
          type="text"
          name="qsTitle"
          v-if="titleChange"
          v-model="titleValue"
          @blur="onblur"
          @keyup.enter="onsubmit"
          ref="titleInput"
        >
        <div class="middle-content">
          <div class="questions" v-for="(qs, index) in qsItem.question">
            <div class="qs-left">
              <p class="qs-title">{{qs.num}}.&nbsp;{{qs.title}}</p>
              <input type="hidden" :data-isNeed="qs.isNeed">
              <p v-for="option in qs.options" class="option" v-if="qs.type !== 'textarea'">
                <label>
                  <input type="radio" :name="`${qs.num}-${qs.title}`" v-if="qs.type === 'radio'">
                  <input
                    type="checkbox"
                    :name="`${qs.num}-${qs.title}`"
                    v-if="qs.type === 'checkbox'"
                  >
                  {{option}}
                </label>
              </p>
              <textarea v-if="qs.type === 'textarea'"></textarea>
            </div>
          </div>
        </div>
      </div>
      <!-- 内容区_E -->
      <!-- 右侧 _S-->
      <div class="right template-control">
        <div class="fill">
          <!-- 单选 -->
          <el-form ref="form" :model="form" label-width="80px">
            <!-- <el-form-item label="问卷标题">
              <el-input v-model="form.name" placeholder="请输入问卷标题"></el-input>
            </el-form-item>-->
            <el-form-item label="题号">
              <el-input placeholder="请输入题号" class="question_number"></el-input>
              <div style="height:10px;"></div>
              <el-alert type="info" description="由字母、_、数字组成，首位字母" show-icon></el-alert>
            </el-form-item>
            <!-- 提示 _S-->
            <!-- 提示_E -->
            <el-form-item label="题目标题">
              <el-input placeholder="请输入题目标题" class="topic"></el-input>
            </el-form-item>
            <table class="gridtable" v-if="op_type!='textarea'">
              <tr>
                <th>序号</th>
                <th>选项</th>
                <th>操作</th>
              </tr>
              <tr v-for="item in tableData">
                <td class="w50">
                  <input type="text" :value="item.s_num" disabled>
                </td>
                <td>
                  <input type="text" :value="item.problem" class="new_option" @blur="sava_option">
                </td>
                <td class="w60">
                  <div>
                    <el-button
                      type="success"
                      icon="el-icon-plus"
                      circle
                      @click="add_option(item.s_num)"
                    ></el-button>
                    <el-button
                      type="success"
                      icon="el-icon-minus"
                      circle
                      @click="del_option(item.s_num)"
                    ></el-button>
                  </div>
                </td>
              </tr>
            </table>
            <el-form-item></el-form-item>
            <el-form-item label="是否必填">
              <el-switch v-model="form.delivery"></el-switch>
            </el-form-item>
            <el-form-item style="text-align:center;">
              <el-button type="primary" @click="save_option">保存</el-button>
              <el-button @click="hide_control">取消</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!-- 右侧_E -->
    </div>
  </div>
</template>

<script>
import storage from "@/vuex/store.js";
export default {
  name: "edit-create",
  data() {
    return {
      text: "",
      is_fill: false,
      is_multiple: false,
      is_single: false,
      qsItem: {},
      over: {},
      save_qsItem: {},
      qsList: storage.get(),
      save_qlist: {},
      isError: false,
      showBtn: false,
      titleChange: false,
      titleValue: "",
      showAddQsDialog: false,
      showAddOptionInput: true,
      qsInputTitle: "",
      qsInputOptions: [],
      info: "",
      selectTime: "",
      addOptionType: "",
      limit: {},
      showDialog: false,
      iterator: {},
      isGoIndex: false,
      option: "",
      form: {
        name: "",
        region: "",
        delivery: false,
        type: [],
        resource: ""
      },
      tableData: [
        {
          s_num: "1",
          problem: "请输入选项"
        }
      ],
      op_type: "",
      index: undefined
    };
  },
  created() {
    console.log((this.index = this.$route.query.num));
    this.fetchData();
  },
  components: {},
  methods: {
    fetchData() {
      let i = 0;

      for (let length = this.qsList.length; i < length; i++) {
        if (this.qsList[i].num == this.$route.query.num) {
          this.qsItem = this.qsList[i];
          this.titleValue = this.qsList[i].title;
          break;
        }
      }
      if (i === this.qsList.length) this.isError = true;
    },
    getSonText(text) {
      this.text = text;
    },
    show_control(event) {
      $(".template-control")
        .css("opacity", "1")
        .addClass("animated bounceInRight");
      const type = $(event.target).attr("data-type");
      this.switch_template(type);
      this.type = "text_item.allert";
    },
    switch_template(type) {
      switch (type) {
        case "fill":
          this.is_fill = true;
          this.is_multiple = this.is_single = false;
          this.op_type = "radio";
          break;
        case "multiple":
          this.is_multiple = true;
          this.is_fill = this.is_single = false;
          this.op_type = "checkbox";
          break;
        case "single":
          this.is_fill = this.is_multiple = false;
          this.is_single = true;
          this.op_type = "textarea";
          break;
        default:
      }
    },
    // 标题_S
    titleClick() {
      this.titleChange = !this.titleChange;
      setTimeout(() => {
        this.$refs.titleInput.focus();
      }, 150);
    },
    onblur() {
      this.titleValue = this.titleValue.trim();
      this.qsItem.title =
        this.titleValue === "" ? this.qsItem.title : this.titleValue;
      this.titleChange = false;
    },
    onsubmit() {
      this.titleValue = this.titleValue.trim();
      this.qsItem.title =
        this.titleValue === "" ? this.qsItem.title : this.titleValue;
      this.titleChange = false;
    },
    // 标题_E
    // 取消
    hide_control() {
      $(".template-control")
        .css("opacity", "0")
        .removeClass("animated bounceInRight");
    },
    // 添加选项
    add_option(option_num) {
      let temporary_option = {},
        num = this.tableData.length,
        problem = this.tableData.length;
      temporary_option.s_num = ++num;
      temporary_option.problem = "请输入选项";
      this.tableData.splice(option_num, 0, temporary_option);
      this.reset_option();
    },
    // 删除选项
    del_option(option_num) {
      this.tableData.splice(option_num - 1, 1);
      this.reset_option();
    },
    // 保存选项
    sava_option() {
      this.tableData.forEach((item, index) => {
        item.problem = $(".new_option")
          .eq(index)
          .val();
      });
    },
    //重置选项
    reset_option() {
      this.tableData.forEach((item, index) => {
        if (item.s_num != index + 1) {
          item.s_num = index + 1;
        }
      });
    },
    // 保存当前编辑的题目
    save_option() {
      let question = {};
      let options = [];

      if ($(".question_number .el-input__inner").val() == "") {
        return alert("题号不能为空");
      }
      if ($(".topic .el-input__inner").val() == "") {
        return alert("题目不能为空");
      }
      //判断题号是否存在
      // 判断题目是否存在
      question.num = $(".question_number .el-input__inner").val();
      question.title = $(".topic .el-input__inner").val();
      this.tableData.forEach((item, index) => {
        options.push(item.problem);
      });
      question.options = options;
      question.isNeed = this.form.delivery;
      this.op_type === "radio" ? (question.type = "radio") : "";
      this.op_type === "checkbox" ? (question.type = "checkbox") : "";
      this.op_type === "textarea" ? (question.type = "textarea") : "";
      // 获取选项
      this.qsItem.question.push(question);
      // 重置tableData
      this.tableData = [{ s_num: "1", problem: "这是第一个选项" }];
    },
    // 保存问卷
    save() {
      this.save_qlist.num = this.qsList.length + 1;
      this.save_qlist.question = this.qsItem;
      if (!this.titleValue) {
        return alert("问卷标题不能为空");
      }
      this.save_qlist.title = this.titleValue;
      // console.log(this.qsList[this.index - 1]);
      // console.log(this.save_qlist.question);
      this.qsList[this.index - 1] = this.save_qlist.question;
      storage.save(this.qsList);
      this.$router.push({ path: "/home" });
    }
  }
};
</script>
<style scoped lang="scss">
@import "./index.scss";
@import "./edit-fill.scss";
@import "./edit.scss";
</style>
<style>
.fill .el-alert .el-alert__description {
  line-height: 20px;
}
.fill .el-form-item__content {
  margin-left: 0 !important;
}
.fill .el-form-item__label {
  width: auto !important;
}
</style>



